.App {
    height: 100vh;
    max-width: 100vw;
    font-family: Arial;
}

.App-header {
    font-size: 1.2rem;
    color: #e0354b;
    display: flex;
    align-items: center;
    border-bottom: 3px solid #e0354b;
}

.App-header img {
    height: 200px;
    pointer-events: none;
}

.App-body {
    margin: 20px;
    height: -webkit-max-content;
    height: max-content;
    padding: 2em;
}

.App-footer {
    text-align: center;
    border-top: 3px solid #e0354b;
    padding: 20px;
    color: rgb(0, 0, 0);
    font-style: italic;
    position: fixed;
    bottom: 0;
    width: 100%;
}

label {
    padding-right: 10px;
}

button {
    background-color: rgb(255, 255, 255);
}

.Notifications {
    padding: 1em;
    border: 2px dashed red;
    position: relative;
    top: 10px;
    width: 95vw;
}

ul li[data-priority="urgent"] {
    color: red;
}

ul li[data-priority="default"] {
    color: blue;
}

